









































































































































































































.draggable-wrap {
  height: 100%;
  .zoom-container {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    text-align: center;
    transform-origin: left top;
  }
  .vdr {
    touch-action: none;
    border: none;

    &:not(.dragging) {
      transition: all 0.1s;
    }
  }
  .tree-org {
    & > .tree-org-node {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &.horizontal > .tree-org-node {
      flex-direction: row;
    }
  }
}
